import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import { connect } from 'react-redux'
import './style.less'

class MallUser extends React.Component {
    constructor(props,context){
       super(props,context);
       this.shouldComponentUpdate=PureRenderMixin.shouldComponentUpdate.bind(this);
       this.state = {
         account:0
       }
    }
	render() {
	    return (
            <div>
               <div className="userInfo">
                  {/*<img src='../../static/images/userImg.jpg'/>*/}
                  <span className="userImg"></span>
                  <p className="name">李汤姆</p>
                  <p className="phone">13567660099</p>
               </div>
               <div className="userList">
                  <div className="userItem">
                     会员等级<span>青铜</span>
                  </div>
	                <div className="userItem">
	                   待支付<span>{(this.state.account).toFixed(2)}元</span>
	                </div>
	            </div>
            </div>

            
	    )
	}
	componentDidMount(){
	   this.getAccount()
	}
	getAccount(){
	   let priceSum=0
       this.props.cartList.map((item,index) => {
          priceSum+=item.num*item.price
       })
       this.setState({
          account:priceSum
       })
	}
}

// -------------------redux react 绑定--------------------
const parseProducts = (products) => {
	let mapProduct = []
	for(let key in products) {
	  mapProduct.push(products[key])
	}
	return mapProduct
}

function mapStateToProps(state) {
    return {
        cartList: parseProducts(state.cart)
    }
}


export default connect(
    mapStateToProps
)(MallUser)


