import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import ReactSwipe from 'react-swipe'
import { connect } from 'react-redux'

import './style.less'

class MallSlider extends React.Component {
    constructor(props,context){
       super(props,context);
       this.shouldComponentUpdate=PureRenderMixin.shouldComponentUpdate.bind(this);
       this.state={
         index:0,
         data:[]
       }
    }
	render(){
      const opt = {
         auto:2000,
         callback:function(index){
             this.setState({index:index});
         }.bind(this)
      }
	  return (
          <div className="swipeWrap">
              {
                     this.props.sliderImg.data
                     ?(
                          <ReactSwipe swipeOptions={opt}>
                            {
                              this.props.sliderImg.data.map((item,index)=>{
                                  return <div className="carousel-item" style={{background:'#ddeeff'}} key={index} >
				                      <img src={item.img}/>
				                  </div>
                              })
				                  
				             }   
				           </ReactSwipe>
                     )                                    
                     : <div className="sliderBone">轮播图加载中...</div>
              }
             
              <div className="index-container">
                  <ul>
                    
                     <li className={this.state.index===0?"selected":""}></li>
                     <li className={this.state.index===1?"selected":""}></li>
                     <li className={this.state.index===2?"selected":""}></li>
                  </ul>
              </div>
          </div>
	  )

	}
	componentDidMount(){
	   this.getImgSrc()
     setTimeout(()=>{console.log(this.props.sliderImg)},3000)
	}
	getImgSrc() {
      if(!this.props.sliderImg.data){
         fetch('https://www.easy-mock.com/mock/5a28a164b5c06d61d33714e9/llxf/react-demo-slider').then((res)=>{
             return res.json()
         }).then((res)=>{
             
             this.props.addSliderImg({data:res.data})
         })
      }
	    
	  
	}
}

// -------------------redux react 绑定--------------------

function mapStateToProps(state) {
    return {
        sliderImg: state.sliderImg
    }
}

function mapDispatchToProps(dispatch) {
    return {
       addSliderImg(data){
          dispatch({type:'sliderImg',data})
       }
    }
}
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(MallSlider)


