import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import { connect } from 'react-redux'
import { hashHistory } from 'react-router';
import './style.less'
import { Link } from 'react-router'

class MallTab extends React.Component {

    constructor(props,context){
       super(props,context);
       this.shouldComponentUpdate=PureRenderMixin.shouldComponentUpdate.bind(this);
       this.state={
         pathName:'/mallHome'
       }
    }
    
	render(){
	  return (
          <div className="Tabwrap">
               <div className="TabItem">
                  <Link  to="/mallHome" className={`${this.props.pathName==='/mallHome'?'active':''}`}>
                    <i className="icon-home"></i>
                    <span>市场</span>
                  </Link>
               </div>
               <div className="TabItem">
                  <Link  to="/mallCart" className={`${this.props.pathName==='/mallCart'?'active':''}`}>
                     <i className="icon-cart"></i>
                     <span>购物车</span>
                     {
                       this.props.flag.cartRead
                       ?''
                       :<strong></strong>
                     }
                  </Link>
               </div>
               <div className="TabItem">
                  <Link  to="/mallUser" className={`${this.props.pathName==='/mallUser'?'active':''}`}>
                     <i className="icon-user"></i>
                     <span>我的</span>
                  </Link>
               </div>
          </div>
	  )
    
	}
  componentDidMount() {
       
       //console.log(this.props.pathName)
    }

	
}


// -------------------redux react 绑定--------------------


function mapStateToProps(state) {
    return {
        flag: state.flag,
    }
}

export default connect(
    mapStateToProps
)(MallTab)


