import React from 'react'
import { Router, Route, IndexRoute } from 'react-router'

import App from '../containers'

import NotFound from '../containers/404'

import LHome from '../containers/LHome'
import mallHome from '../containers/mallHome'
import MallCart from '../containers/mallCart'
import MallCity from '../containers/mallCity'
import MallDtl from '../containers/mallDtl'
import MallUser from '../containers/mallUser'


class RouterMap extends React.Component {
    render() {
        return (
            <Router history={this.props.history}>
                <Route path='/' component={App}>
                    <IndexRoute component={mallHome}/>
					<Route path='/LHome' component={LHome}/>
                    <Route path='/mallHome' component={mallHome}/>
                    <Route path='/mallCart' component={MallCart}/>
                    <Route path='/MallCity' component={MallCity}/>
                    <Route path='/MallDtl/:id' component={MallDtl}/>
                    <Route path='/mallUser' component={MallUser}/>
                    <Route path='*' component={NotFound}/>
                </Route>
            </Router>
        )
    }
}

export default RouterMap



