import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import MallHeader from '../../components/mallHeader'
import { connect } from 'react-redux'
import {proAddToCart} from '../../actions/product_addToCart'
import {cartProducts} from '../../actions/cart'
import { hashHistory  } from 'react-router'  //引入路由函数
import './style.less'
// 定义组件 
class mallDtl extends React.Component {
    constructor(props,context){
       super(props,context);
       this.shouldComponentUpdate=PureRenderMixin.shouldComponentUpdate.bind(this);
       this.state = {
         dtlData:null,
         mockData:null
       }
    }
   render() { // return 里面写jsx语法 

      return ( 
        <div>
          <MallHeader title="详情" back={true}/>
          {
             this.state.dtlData!= null
             ?(
               <div>
    					    <div className="imgBox">
    			            <img src={this.state.dtlData.src}/>
    			        </div>
    			        <div className="contentBox">
    	                    <h4>{this.state.dtlData.name}</h4>
    	                    <p><span>商品编号：{this.state.dtlData.id}</span><span>单价：{this.state.dtlData.price}元</span></p>
    	                    <p><span>剩余：{this.state.dtlData.inventory}件</span><span>备注：{this.state.dtlData.src.slice(22,30)}</span></p>
    			        </div>
    			        <div className="disc">
                            <h5 style={{"fontSize":"14px"}}>商品描述:</h5>
                            <p>{this.state.mockData?this.state.mockData[0].cparagraph:'加载中...'}</p>
    			        </div>
                  <div className="bottomToolBox">
                     <span className="addCartBtn" onClick={this.addToCart.bind(this,this.state.dtlData)}>加入购物车</span>
                  </div>
			        </div>
             )
             :<div className="loadTip">正在加载中....</div>
          }

          
        </div>

      )
   }
   componentDidMount(){
     this._isMounted = true
     const id=this.props.params.id
     this.setState({
            dtlData:this.props.products[id]
     })
     console.log(this.props.products)
     this.getList()
   } 
   componentWillUnmount() {
        this._isMounted = false
   }
   //原根据url参数获取商品详情数据，现在只用来获取一段随机的文本内容
   getList(){
	   fetch('https://www.easy-mock.com/mock/5a28a164b5c06d61d33714e9/llxf/react-demo-list').then((res)=>{
	       return res.json()
	   }).then((res)=>{
        if (this._isMounted) {
  	       this.setState({
  	         mockData:res.data
  	       })
        }
	   })
	 }

   addToCart(item){
     if(this.props.cartInfo[item.id]){
        hashHistory .push('/mallCart')
     }else{
       this.props.addToCart_action(item)
       hashHistory .push('/mallCart')
     }
     
   }
}


// -------------------redux react 绑定--------------------
const parseProducts = (products) => {
  let mapProduct = []
  for(let key in products) {
    mapProduct.push(products[key])
  }
  return mapProduct
}

function mapStateToProps(state) {
    return {
        products:state.products,
        cartInfo: state.cart
    }
}
function mapDispatchToProps(dispatch) {
    return {
       addToCart_action(item){
          dispatch(proAddToCart(item.id))
          dispatch(cartProducts(item))
       }
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(mallDtl)


