const initialState = {flagStr:'1111',cartRead:true}

export default function flag (state = initialState, action) {
    switch (action.type) {
        case 'changeFlag':
            console.log('redux>reducers>flag.js:修改flag成功！！！')
            console.log(action.data)
            //return {flagStr:'22222'}
            return {...state,flagStr:action.data}
        case 'cartUpdate':
            return {...state,cartRead:false}
        case 'cartRead':
            return {...state,cartRead:true}
        default:
            return state
    }
}


